<template>
  <div class="w-full h-full">
    <navbar/>
    <div class="flex flex-1 flex-wrap">
        <keep-alive>
            <div v-for="product in products" :key="product.id" class="w-1/5">
                {{ product.Name }}
            </div>
        </keep-alive>
    </div>
  </div>
</template>

<script>
import Navbar from '@/components/objects/Navbar'

export default {
    name: 'Index',

    mounted() {
        axios.get('/api/products/all').then(response => {
            this.products = response.data
        })
    },

    data() {
        return {
            products: []
        }
    },

    components: {
        'navbar': Navbar
    }
}
</script>




